export default {
	data() {
		return {
			dateTime1: '2019-00-00',
			dateTime2: '2019-00-00',
			jiui: true,
			yesOrNoName: null,
			title: '',
			content: '',
			zuqqNum: '',
			money: '',
			user_token: ''
		}
	},
	onLoad() {
		let token = uni.getStorageSync("user_token");
		this.user_token = token;
		console.log("this.user_token: " + this.user_token);
	},
	methods: {
		DateChange(e) {
			this.dateTime1 = e.detail.value
		},
		DateChange2(e) {
			this.dateTime2 = e.detail.value
		},
		jiUiSwit(e) {
			this.jiui = e.detail.value
		},
		showModal(e) {
			let that = this;
			if (that.title.length == 0) {
				uni.showToast({
					title: '标题为空',
					icon: 'none'
				});
			} else {
				if (that.content.length == 0) {
					uni.showToast({
						title: '内容为空',
						icon: 'none'
					});
				} else {
					if (that.zuqqNum.length == 0) {
						uni.showToast({
							title: '召集人数为空',
							icon: 'none'
						});
					} else {
						if (that.money.length == 0) {
							uni.showToast({
								title: '价格为空',
								icon: 'none'
							});
						} else {
							if (!that.jiui) {
								console.log("that.dataTime1: " + that.dateTime1);
							}
							this.yesOrNoName = e.currentTarget.dataset.target
						}
					}
				}
			}


		},
		hideModal(e) {
			this.yesOrNoName = null
		},
		addMission() {

		},
		hide() {
			this.yesOrNoName = null
		},
		yes() {
			let that = this;
			that.yesOrNoName = null;
			if (!that.jiui) {
				console.log("that.dataTime1: " + that.dateTime1);
				uni.request({
					url: that.MisUrl + '/mis/userAddMis',
					method: 'POST',
					header: {
						'user_token': that.user_token
					},
					data: {
						'title': that.title,
						'content': that.content,
						'zuqqNum': that.zuqqNum,
						'money': that.money,
						'jiui': '0',
						'time1': that.dateTime1,
						'time2': that.dateTime2
					},
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			} else {
				uni.request({
					url: that.MisUrl + '/mis/userAddMis',
					method: 'POST',
					header: {
						'user_token': that.user_token
					},
					data: {
						'title': that.title,
						'content': that.content,
						'zuqqNum': that.zuqqNum,
						'money': that.money,
						'jiui': '1'
					},
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}


		}

	}
}
